s-serif !important;\n    text-align: center;\n    height: 100%;\n    overflow: hidden;\n    background: url('/assets/lambda/images/login-bg.jpg') no-repeat;\n    background-size: cover;\n    background-position: center top;\n}\n\n@media only screen and (min-width: 308px) {\n}\n\n.fill {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n}\n\n.aside-theme {\n    margin: 0;\n    width: 100%;\n    height: 100%;\n    min-height: 100vh;\n    display: flex;\n    background-size: cover;\n    background-position: center top;\n    .content {\n        flex: 1;\n        display: flex;\n        position: relative;\n        height: 100vh;\n        &:before {\n            position: absolute;\n            background-color: rgba(38, 50, 56, .6);\n            height: 100%;\n            width: 100%;\n            display: block;\n            content: \"\";\n        }\n\n        .title {\n            z-index: 10;\n            color: hsla(0, 0%, 100%, .8);\n            margin: 300px 0 0 100px;\n            text-align: left;\n            font-family: 'Roboto Condensed', sans-serif;\n            h2 {\n                font-weight: 500;\n                font-size: 36px;\n                margin-bottom: 10px;\n                line-height: 36px;\n            }\n            p {\n                font-weight: 300;\n                font-size: 14px;\n                font-family: 'Roboto Condensed', sans-serif;\n            }\n        }\n    }\n\n    .auth {\n        box-shadow: -2px 0px 5px 1px rgba(#222, .2);\n        align-self: flex-end;\n        background: #ffffff;\n        position: relative;\n        z-index: 0;\n        width: 450px;\n        max-width: 450px;\n        height: 100vh;\n        overflow: auto;\n        margin-left: auto;\n        .lang-switcher {\n            position: absolute;\n            top: 25px;\n            right: 20px;\n            z-index: 1000;\n            a {\n                text-decoration: none;\n                color: #565656;\n                padding: 6px 12px;\n                font-size: 13px;\n                border: solid 1px transparent;\n                margin-right: 5px;\n                cursor: pointer;\n                border-radius: 18px;\n                &:last-child {\n                    border-right: 0;\n                }\n                &:hover {\n                    background: #f4f5f6;\n                }\n                &.active {\n                    border: solid 1px $color-primary;\n                    color: $color-primary;\n                }\n            }\n        }\n\n        .logo {\n            width: 100%;\n            text-align: left;\n            height: 100px;\n            img {\n                height: 60px;\n            }\n            &.with-text {\n                display: flex;\n                width: 100%;\n                align-items: center;\n                img {\n                    height: 60px;\n                    width: auto !important;\n                    margin-right: 20px;\n                }\n                span {\n                    font-size: 12px;\n                    font-weight: 700;\n                    line-height: 20px;\n                    text-transform: uppercase;\n                    max-width: 220px;\n                }\n            }\n        }\n    }\n\n    .form-wrap {\n        width: 100%;\n        margin: 2em auto 0;\n        position: absolute;\n        top: 0;\n        bottom: 0;\n        .form-content {\n            padding: 1.5em 3em;\n            text-align: left;\n            width: auto;\n            h2 {\n                font-size: 18px;\n                margin: 20px 0 25px;\n                padding: 0;\n                color: $color-primary;\n                text-transform: uppercase;\n                font-weight: 500;\n            }\n            #msg {\n                .error {\n                    color: $color-danger;\n                }\n                .success {\n                    color: $color-success;\n                }\n                font-size: 12px;\n                text-align: center;\n                margin-top: 20px;\n                transition: .25s ease-in-out;\n            }\n            form {\n                min-width: 300px !important;\n                .input {\n                    position: relative;\n                    margin: 0 0 .8em 0;\n                    .icon {\n                        width: 16px;\n                        height: 16px;\n                        background: url(../../../images/icon.png) no-repeat -3px -4px;\n                        position: absolute;\n                        left: 6px;\n                        top: 8px;\n                        opacity: 0.6;\n                        cursor: text;\n                        z-index: 10;\n                        display: block;\n                        transition: .25s ease-in-out;\n                        &.pass {\n                            background: url(../../../images/icon.png) no-repeat -3px -83px;\n                        }\n                    }\n                    input {\n                        padding: 8px 10px 8px 28px;\n                        font-size: 14px;\n                        width: 100%;\n                        display: inline-block;\n                        background: transparent;\n                        border: 1px solid #dedede;\n                        border-radius: 4px;\n                        outline: 0;\n                        box-sizing: border-box;\n                        -moz-box-sizing: border-box;\n                        font-family: inherit;\n                        color: #666666;\n                        transition: .25s ease-in-out;\n                        &:disabled {\n                            opacity: .5;\n                            &:hover,\n                            &:active,\n                            &:focus {\n                                border-bottom: 1px solid #cccccc;\n                            }\n                        }\n                        &:active,\n                        &:focus {\n                            border: 1px solid $color-primary;\n                            & + span {\n                                opacity: 1;\n                            }\n                        }\n                        &::placeholder {\n                            /* Chrome, Firefox, Opera, Safari 10.1+ */\n                            color: #999999;\n                        }\n                    }\n                }\n                .button {\n                    margin: 1em 0;\n                    height: 30px;\n                    width: 130px;\n                    display: inline-flex;\n                    align-items: center;\n                    justify-content: center;\n                    background-color: $color-primary;\n                    border: none;\n                    color: #fff;\n                    font-size: 13px;\n                    border-radius: 30px;\n                    box-shadow: none;\n                    outline: none;\n                    font-weight: 300;\n                    transition: .25s ease-in-out;\n                    position: relative;\n                    cursor: pointer;\n                    span.loader {\n                        position: absolute;\n                        right: 5px;\n                    }\n                    &:hover {\n                        background-color: darken($color-primary, 5%);\n                    }\n                }\n                .action {\n                    padding: .8em 0 0;\n                    font-size: .93em;\n                    p {\n                        float: right;\n                        text-align: left;\n                        font-size: 13px;\n                        margin-top: 15px;\n                        a {\n                            color: $color-primary;\n                            text-decoration: underline;\n                            &:hover {\n   