 }\n\n    .content {\n        //position: fixed;\n        z-index: 1;\n        float: none;\n        margin: 0 auto;\n        width: 100%;\n        height: 40px;\n        -webkit-box-shadow: none;\n        -moz-box-shadow: none;\n        box-shadow: none;\n        overflow: hidden;\n        .content-blur {\n            @extend .fill;\n            background: url('/assets/lambda/images/blur-bg.jpg') no-repeat;\n            background-size: cover;\n            filter: blur(30px);\n            height: 500px;\n        }\n        .content-color-layer {\n            @extend .fill;\n            background: rgba($color-primary, .8);\n        }\n\n    }\n\n    /* SLIDESHOW */\n    #slideshow {\n        position: relative;\n        margin: 0 auto;\n        width: 100%;\n        height: 100%;\n        padding: 10px 0;\n        border-radius: 10px 0 0 10px;\n        h2 {\n            text-align: center;\n            font-size: 1.4em;\n            color: #f1f1f1;\n            font-weight: 400;\n        }\n        p {\n            color: #f1f1f1;\n            display: none;\n            font-weight: 300;\n            font-size: 14px;\n        }\n        div {\n            position: absolute;\n            top: 0;\n            left: 0;\n            right: 0;\n            bottom: 0;\n            padding: 1em 0;\n            background-repeat: no-repeat;\n            -webkit-background-size: cover;\n            -moz-background-size: cover;\n            -o-background-size: cover;\n            background-size: cover;\n        }\n        .one {\n            background-image: url(\"/assets/lambda/images/dots.png\");\n            background-repeat: no-repeat;\n            background-position: 0% 50%;\n        }\n        .two {\n            background-image: url(\"/assets/lambda/images/gears.png\");\n            background-repeat: no-repeat;\n            background-position: 0% 50%;\n        }\n        .three {\n            background-image: url(\"/assets/lambda/images/splat.png\");\n            background-repeat: no-repeat;\n            background-position: 0% 5%;\n        }\n    }\n\n    /* auth (FORM WRAPPER) */\n\n    .auth {\n        position: relative;\n        z-index: 0;\n        float: none;\n        margin: 0 auto;\n        padding-top: 40px;\n        width: 100%;\n        height: 100vh;\n        overflow: auto;\n        border-radius: 0; //border-top: 1px solid #4FC1B7;\n        .lang-switcher {\n            position: absolute;\n            top: 25px;\n            right: 20px;\n            a {\n                text-decoration: none;\n                color: #565656;\n                padding: 6px 12px;\n                font-size: 13px;\n                border: solid 1px transparent;\n                margin-right: 5px;\n                cursor: pointer;\n                border-radius: 18px;\n                &:last-child {\n                    border-right: 0;\n                }\n                &:hover {\n                    background: #f4f5f6;\n                }\n                &.active {\n                    border: solid 1px $color-primary;\n                    color: $color-primary;\n                }\n            }\n        }\n\n        .logo-container {\n            width: 100%;\n            margin-top: -40px;\n            text-align: left;\n            height: 100px;\n            padding-left: 80px;\n            img {\n                height: 60px;\n            }\n        }\n        .auth-blur {\n            @extend .fill;\n            background: url('/assets/lambda/images/blur-bg.jpg') no-repeat;\n            background-size: cover;\n            filter: blur(30px);\n        }\n        .auth-color-layer {\n            @extend .fill;\n            background: rgba(#ffffff, .95);\n        }\n    }\n\n    /* FORM ELEMENTS */\n\n    input {\n        font: 16px/26px \"Raleway\", sans-serif;\n    }\n\n    .form-wrap {\n        width: 100%;\n        margin: 2em auto 0;\n        position: absolute;\n        top: 0;\n        bottom: 0;\n      